import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service'
import { HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers: [WeatherService]
})
export class WeatherComponent implements OnInit {

  result ;
  txtCityName ='';
  cityName = '';
  todos ;
  isLoading = false ;

  constructor(private weatherSevice: WeatherService, private http : HttpClient) { }

  ngOnInit() {}

  runGetWeatherByEnter(event){
    this.todos = '';
    event.keyCode === 13 ? this.getWeather() : {} ;
  }

  getWeather(){
    // const params = {
    //     cityName: this.cityName
    // }
    this.isLoading = true;
    this.result = this.weatherSevice.getWeather(this.txtCityName).subscribe(data => {
        this.isLoading = false;
       this.todos = data["main"]["temp"];
    });
  }

}
