import { NgModule } from "@angular/core";
import { Routes, RouterModule } from '@angular/router';
import { ContactsModule } from './contacts/contacts.module';

import { ContactsDetailComponent } from './contacts-detail/contacts-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { CommonModule } from '@angular/common';

const routerConfig: Routes = [
    { path: 'detail/:id/:name/:phoneNumber', component: ContactsDetailComponent },
    { path: 'detail', component: ContactsDetailComponent },
    { path: '', redirectTo: '/contacts', pathMatch: 'full' },
    { path: "**", component: PageNotFoundComponent }
  ];

  @NgModule({
      imports: [
        ContactsModule,
        RouterModule.forRoot(routerConfig), // nếu để config này trc ContactsModule thì nó sẽ nhảy vào config trc, nên nó nhảy vào "**"
        CommonModule    
    ],
      declarations: [
          PageNotFoundComponent,
          ContactsDetailComponent
      ],
      exports: [RouterModule]
  })

  export class AppRoutingModule {}
